section
{
    display: flex;
    width: 100%;
    height: auto;
    flex-wrap: wrap;
     justify-content: center;
}

section img
{
    width: 200px;
     flex-grow: 1;   
     /*  object-fit: cover;  */  
   opacity: 0.8;
   transition: 0.5s ease;

   


}

section img:hover
{
 cursor: crosshair;
 width: 350px;
 opacity: 1;
 filter: contrast(120%);

}